.slanted-txt-bg {
    /*
    background: $color-sport-red;
    color: $color-purewhite;
    display: inline-block;
    position: relative;
    z-index: z-index("default");
    margin-left: $measure--threequarters; // 0.75rem ~ 10px
    */
    font-size: 2.5rem;
    font-weight: bold;
    line-height: 1.2;
    color: white;

    .slanted-line {
        position: relative;
        display: inline-block;
        padding: 0.01rem 0.2em;
        margin-bottom: -0.4em;
        z-index: 1;

        &:before {
            /*
            display: block;
            content: "";
            width: calc(100% + $measure--small2);
            transform: translateX(-$measure--threequarters) skew(-20deg);
            height: 100%;
            position: absolute;
            background: $color-sport-red;
            z-index: z-index("below");
            */

            content: "";
            position: absolute;
            top: 0;
            left: -5px;
            right: -5px;
            bottom: 0;
            background-color: #d62828;
            transform: skewX(-20deg);
            z-index: -1;
            border-radius: 2px;
        }
    }
}

